// export default Footer;

import React from 'react';
import { Typography } from '@mui/material';
import { styled } from '@mui/system';

const FooterContainer = styled('footer')(({ theme }) => ({
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
    flexWrap: 'wrap',
     background: '#E5E5E5',
  flexDirection: 'column',
  [theme.breakpoints.up('md')]: {
    flexDirection: 'row',
  },
}));

const ImageContainer = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  width: '40%',
  [theme.breakpoints.down('md')]: {
    width: '100%',
  },
}));

const TextContainer = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  width: '40%',
  color: '#E04E57',
  fontSize: '1rem',
  [theme.breakpoints.down('md')]: {
    width: '100%',
    marginTop: '20px',
  },
}));

const Footer = () => {
  return (
    <FooterContainer>
      <ImageContainer>
        {/* Ajoutez votre composant d'image ici */}
        <img src="./images/fr.png" alt="FooterImage" />
      </ImageContainer>
      <TextContainer>
        <Typography variant="subtitle2" align="justify">
       DANS LE CAS D'VITER TOUT LITIGE ENTRE PARTICULIERS OU PROFESSIONNELS LORS D'UNE VENTE OU D'UN ACHAT DE BIENS, LE
       MINISTRE DE L'CONOMIE ET DES FINANCES EN PARTENARIAT AVEC LES OPRATEURS VISA - MASTERCARD ONT MIS EN PLACE CE
        SERVICE VOUS PERMETTANT DE TLCHARGER UNE ATTESTATION SERVANT DE TRACE JURIDIQUE DE LA TRANSACTION.
        </Typography>
      </TextContainer>
    </FooterContainer>
  );
};

export default Footer;

