import React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import { styled } from '@mui/material/styles';
import { Link } from 'react-router-dom';
import TextLoop from 'react-text-loop';
import { Box, Button } from '@mui/material';

const HeaderContainer = styled(AppBar)`
  background: whitesmoke;
`;

const Header = () => {
  return (
    <HeaderContainer position="static">
      <Toolbar>
        <div
          style={{
            width: '100%',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            height: 'auto',
            color: '#E04C55',
          }}
        >
          <Typography variant="body1" sx={{ mb: 4 }}>
            Attester ma rcharge en 2 minutes
          </Typography>
          <Typography variant="h4" sx={{ mb: 1, fontWeight: 'bolder' }}>
            Simple pour attester vos recharges
          </Typography>
          <TextLoop interval={3000}>
            <Typography variant="body1" sx={{ mb: 4 }}>
              <Link to="/attestation-pcs" style={{ textDecoration: 'none', color: 'inherit' }}>
                ATTESTATION PCS
              </Link>
            </Typography>
            <Typography variant="body1" sx={{ color: '#CF7131', mb: 4 }}>
              <Link to="/attestation-transcash" style={{ textDecoration: 'none', color: 'inherit' }}>
                ATTESTATION TRANSCASH
              </Link>
            </Typography>
          </TextLoop>
          <Typography sx={{ color: 'gray' }} variant="body1">
            Votre recharge, votre Attestation en main. Votre garantie. Vrifiez votre recharge en toute scurit
          </Typography>
          <Box sx={{ mb: 4 }}>
            <Button
              component={Link}
              to="/attestation-pcs"
              sx={{ background: '#E04C55', margin: '5px' }}
              variant="contained"
            >
              ATTESTATION PCS
            </Button>
            <Button
              component={Link}
              to="/attestation-transcash"
              sx={{ margin: '5px', background: '#CF7131' }}
              variant="contained"
            >
              ATTESTATION TC
            </Button>
          </Box>
        </div>
      </Toolbar>
    </HeaderContainer>
  );
};

export default Header;
